// import common from "../../common/common.js"
import trailerStars from '../../components/trailerStars.vue';
import titleBar from '../../components/titleBar.vue';
export default {
	data() {
		return {
			meuntitle: [
				{
					title: '热门超英',
					icon: '../../static/icos/hot.png'
				},
				{
					title: '热门预告',
					icon: '../../static/icos/interest.png'
				},
				{
					title: '猜你喜欢',
					icon: '../../static/icos/guess-u-like.png'
				}
			],
			carouselList: [],
			hotSuperheroList: [],
			hotTrailerList: [],
			guessULikeList: [],
			animationData: {},
			animationDataArr: [{}, {}, {}, {}, {}]
		};
	},
	onUnload() {
		//页面卸载的时候，清除动画数据
		this.animationData = {};
		this.animationDataArr = [{}, {}, {}, {}, {}];
	},
	onPullDownRefresh() {
		this.refresh();
	},
	onHide() {
		if (this.videoContext) {
				this.videoContext.pause();
			}
	},
	onLoad() {
		var me = this;


		// 在页面创建的时候，创建一个临时动画对象
		this.animation = uni.createAnimation();

		// 获取common.js中的服务器地址
		// var serverUrl = common.serverUrl;
		// 通过挂载到main.js中获取服务器的地址，作为全局变量
		var serverUrl = this.serverUrl;
		//请求轮播图数据
		uni.request({
			url: serverUrl + '/index/carousel/list', //仅为示例，并非真实接口地址。
			method: 'POST',
			header: {
				'content-type': 'application/x-www-form-urlencoded'
			},
			data: {
				qq: '466481615'
			},
			success: res => {
				// console.log(res.data);
				//获取真实数据之前，务必判断状态是否为200
				if (res.data.status == 200) {
					var carouselList = res.data.data;
					me.carouselList = carouselList;
				}
			}
		});
		// 查询热门超英
		uni.request({
			url: serverUrl + '/index/movie/hot',
			method: 'POST',
			header: {
				'content-type': 'application/x-www-form-urlencoded'
			},
			data: {
				type: 'superhero',
				qq: '466481615'
			},
			success: res => {
				// console.log(res.data);
				// 获取真实数据之前，务必判断状态是否为200
				if (res.data.status == 200) {
					var hotTrailerList = res.data.data;
					me.hotTrailerList = hotTrailerList;
				}
			}
		});
		// 热门超英预告片
		uni.request({
			url: serverUrl + '/index/movie/hot',
			method: 'POST',
			header: {
				'content-type': 'application/x-www-form-urlencoded'
			},
			data: {
				type: 'trailer',
				qq: '466481615'
			},
			success: res => {
				// console.log(res.data);
				// 获取真实数据之前，务必判断状态是否为200
				if (res.data.status == 200) {
					var hotSuperheroList = res.data.data;
					me.hotSuperheroList = hotSuperheroList;
				}
			}
		});

		this.refresh();
	},
	methods: {
		meIsPlaying(e) {
			//播放一个视频的时候，需要暂停其他正在播放的视频
			var me = this;
			var trailerId = "";
			if(e) {
				trailerId = e.currentTarget.dataset.playingindex;
				me.videoContext = uni.createVideoContext(trailerId);
			}
			var hotTrailerList = me.hotTrailerList;
			for (var i = 0; i < hotTrailerList.length ; i ++) {
				var tempId = hotTrailerList[i].id;
				//如果视屏id不等于当前播放的id，暂停他们的视频
				if (tempId != trailerId) {
						uni.createVideoContext(tempId).pause();
					}
			}
		},
		refresh() {
			uni.showLoading({
				title: '加载中...',
				mask: true
			});
			// uni.showNavigationBarLoading();

			var serverUrl = this.serverUrl;
			// 猜你喜欢
			uni.request({
				url: serverUrl + '/index/guessULike',
				method: 'POST',
				header: {
					'content-type': 'application/x-www-form-urlencoded'
				},
				data: {
					qq: '466481615'
				},
				success: res => {
					// console.log(res.data);
					// 获取真实数据之前，务必判断状态是否为200
					if (res.data.status == 200) {
						var guessULikeList = res.data.data;
						this.guessULikeList = guessULikeList;
					}
				},
				complete: () => {
					// uni.hideNavigationBarLoading();
					uni.hideLoading();
					uni.stopPullDownRefresh();
				}
			});
		},
		//实现点赞动画效果
		praiseMe(e) {
			// console.log(e)

			var gIndex = e.currentTarget.dataset.gindex;

			//构建动画数据，并且通过step来表示这组动画的完成
			this.animation
				.translateY(-60)
				.opacity(1)
				.step({
					duration: 400
				});
			//导出动画数据到view组件，实现组件的动画效果
			// this.animationData = this.animation.export();
			this.animationData = this.animation;
			this.animationDataArr[gIndex] = this.animationData.export();

			//还原动画
			setTimeout(() => {
				this.animation
					.translateY(0)
					.opacity(0)
					.step({
						duration: 0
					});
				// this.animationData = this.animation.export();
				this.animationData = this.animation;
				this.animationDataArr[gIndex] = this.animationData.export();
			}, 500);


		}
	},
	components: {
		trailerStars,
		titleBar
	}
};
